/*------------------------------------------*/
/*  LOGO
/*------------------------------------------*/
.logo {
  display: inline-block;
  padding: 14px 10px;
}

.logo:hover, .logo:focus {
  text-decoration: none;
}

.logo--header {
  margin-right: auto;
}

@media (min-width: 1200px) {
  .logo--header {
    margin-right: 30px;
  }
}

.logo img {
  display: inline-block;
  margin-right: 6px;
}

.logo-text {
  display: none;
  vertical-align: middle;
  font-weight: bold;
  line-height: 10px;
  font-size: 9px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.logo-text--big {
  font-weight: bold;
  font-family: "Fira Sans", sans-serif;
  font-size: 20px;
  letter-spacing: 0.17em;
  line-height: 1;
}

@media (min-width: 450px) {
  .logo-text {
    display: inline-block;
  }
}

.hamburger {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding: 30px;
  outline: 0;
  z-index: 10000;
  cursor: pointer;
}

@media (min-width: 1200px) {
  .hamburger {
    display: none;
  }
}

.hamburger:hover .bar,
.hamburger:focus .bar {
  background-color: #fff;
}

.bar {
  opacity: 1;
  position: absolute;
  left: 50%;
  margin-left: -11px;
  width: 20px;
  height: 2px;
  background-color: #d8d8d8;
  transition: opacity 0.4s, transform 0.4s, background-color 0.15s ease-in-out;
}

.bar--1 {
  top: 23px;
}

.bar--2, .bar--3 {
  top: 50%;
  transform: translateY(-50%);
}

.bar--4 {
  bottom: 23px;
}

.open .bar--1,
.open .bar--4 {
  opacity: 0;
}

.open .bar--2 {
  transform: rotate(-45deg);
}

.open .bar--3 {
  transform: rotate(45deg);
}

/*------------------------------------------*/
/*  SEARCH FORM
/*------------------------------------------*/
.search-form .field-container {
  position: relative;
  width: 432px;
  height: 32px;
  border-radius: 25px;
}

.search-form .field-container::after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 16px;
  width: 18px;
  height: 18px;
  background-image: url(../img/icons/ic-search.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.search-form .search-field {
  display: block;
  width: 100%;
  height: 32px;
  padding: 8px 40px 8px 16px;
  border: 0;
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.05);
  font-size: 13px;
  font-style: italic;
  color: #fff;
  transition: background 0.3s ease-in-out;
}

.search-form .search-field:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.15);
}

.search-form--desktop {
  display: none;
}

@media (min-width: 1200px) {
  .search-form--desktop {
    display: block;
  }
}

.search-form--mobile {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  width: 100%;
  padding: 15px;
  background-color: #353535;
  z-index: 100;
}

@media (min-width: 576px) {
  .search-form--mobile {
    width: 450px;
  }
}

.search-form--mobile .field-container {
  width: 100%;
  height: 38px;
  border-radius: 30px;
}

.search-form--mobile .field-container::after {
  content: none;
}

.search-form--mobile .search-field {
  width: 100%;
  height: 38px;
  border-radius: 30px;
}

.search-form--big {
  display: none;
  margin-bottom: 40px;
}

@media (min-width: 768px) {
  .search-form--big {
    display: block;
  }
}

.search-form--big .field-container {
  width: 100%;
}

.search-form--big .field-container::after {
  background-image: url(../img/icons/ic-search-dark.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.search-form--big .search-field {
  color: #353535;
  font-style: normal;
  background: #DDE4EE;
}

.search-form--big .search-field:focus {
  background: #DDE4EE;
}

/*------------------------------------------*/
/*  BUTTONS
/*------------------------------------------*/
.button {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 8px 15px;
  user-select: none;
  border: 0;
  border-radius: 5px;
  font-family: "Ubuntu", sans-serif;
  font-size: 0.8125rem;
  line-height: 1.2;
  letter-spacing: 0.08em;
  font-weight: bold;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  text-transform: uppercase;
  transition: color 0.15s ease-in-out, background 0.15s ease-in-out;
}

.button:focus {
  outline: none;
}

.button-primary {
  padding: 8px 15px;
  border: 1.5px solid #25389C;
  background: #25389C;
}

.button-primary:hover, .button-primary:focus {
  color: #fff;
  background: #102074;
}

.button-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.8);
}

.button-secondary:hover, .button-secondary:focus {
  background: rgba(255, 255, 255, 0.2);
}

/*------------------------------------------*/
/*  NAVIGATION
/*------------------------------------------*/
.nav-main ul {
  list-style: none;
}

.nav-main li {
  display: inline-block;
  margin-right: 30px;
}

.nav-main li a {
  position: relative;
  display: block;
  padding: 18px 0;
  font-family: "Ubuntu", sans-serif;
  font-weight: bold;
  line-height: 1;
  font-size: 0.8125rem;
  letter-spacing: 0.08em;
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  transition: color 0.15s ease-in-out;
}

.nav-main li a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 2px;
  background: #DB3131;
  transition: width 0.3s;
}

.nav-main li .current-item,
.nav-main li a:hover,
.nav-main li a:focus {
  color: #fff;
  text-decoration: none;
}

.nav-main li .current-item::after,
.nav-main li a:hover::after,
.nav-main li a:focus::after {
  width: 100%;
}

@media (min-width: 1200px) {
  .nav-mobile {
    display: none;
  }
}

.nav-mobile ul {
  list-style: none;
}

.nav-mobile a {
  display: block;
  color: #fff;
  font-weight: bold;
  transition: color 0.15s ease-in-out;
}

